/* Header azul en Home */
.home-header-row {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 16px;
  margin-top: 24px;
  margin-bottom: 0;
  font-size: 2rem;
}
.home-header-row img {
  width: 48px;
  height: 48px;
}

@media (max-width: 600px) {
  .home-header-row {
    font-size: 20px
  }
  .home-header-row img {
    width: 36px;
    height: 36px;
  }
}
@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@600;700&family=Nunito+Sans:wght@400&display=swap');

.birdlist-root {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #FFD54F;
  min-height: 100vh;

.birdlist-header-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  gap: 32px;
  margin-bottom: 24px;
}

.bird-counter {
  display: flex;
  align-items: center;
  background: rgba(255,255,255,0.95);
  border-radius: 24px;
  padding: 6px 16px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  position: fixed;
  top: 16px;
  right: 16px;
  z-index: 100;
}

@media (max-width: 600px) {
  .bird-counter {
    top: 8px;
    right: 8px;
    padding: 4px 10px;
    border-radius: 18px;
    box-shadow: 0 1px 4px rgba(0,0,0,0.10);
    min-width: 60px;
    min-height: 28px;
  }
  .bird-counter-icon {
    width: 22px;
    height: 22px;
  }
  .bird-counter-number {
    font-size: 1rem;
    margin: 0 6px;
    padding: 0;
  }
}
}
@media (max-width: 400px) {
  .bird-counter {
    top: 2px;
    right: 2px;
    padding: 1px 4px;
    min-width: 28px;
    min-height: 14px;
  }
  .bird-counter-icon {
    width: 12px;
    height: 12px;
  }
  .bird-counter-number {
    font-size: 0.55rem;
    margin: 0 1px;
  }
  .birdlist-subtitle {
    margin-top: 32px;
  }
}

.bird-counter-icon {
  width: 28px;
  height: 28px;
}

.bird-counter-number {
  font-family: 'Baloo 2', sans-serif;
  font-size: 1.3rem;
  color: #1AB6D9;
  font-weight: bold;
  margin: 0 12px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.birdlist-header {
  width: 100%;
  background: #1AB6D9;
  color: #fff;
  font-family: 'Baloo 2', sans-serif;
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
  padding: 32px 0 16px 0;
  margin-bottom: 0;
  letter-spacing: 1px;
}

.birdlist-subtitle {
  background: #FFD54F;
  color: #333333;
  font-family: 'Baloo 2', sans-serif;
  font-size: 1.5rem;
  font-weight: 600;
  text-align: center;
  padding: 16px 32px 12px 32px;
  border-radius: 16px;
  letter-spacing: 0.5px;
  margin: 0;
}

@media (max-width: 600px) {
  .birdlist-subtitle {
    margin-top: 2%
  }
}


.card {
  background-color: #fff2c9;
  border-radius: 20px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  overflow: hidden;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  cursor: pointer;
  max-width: 316px;
  margin: 0 auto;
  border: none;
  position: relative;
  display: flex;
  flex-direction: column;
}

.card:hover {
  transform: scale(1.02);
  box-shadow: 0 6px 14px rgba(0,0,0,0.15);
}

.card-img {
  width: 100%;
  height: 180px;
  object-fit: cover;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}

.card-content {
  padding: 12px;
  text-align: center;
}

.card-title {
  font-family: 'Baloo 2', sans-serif;
  font-size: 1.2rem;
  color: #333333;
  margin: 4px 0;
  font-weight: 600;
}

.card-subtitle {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 0.9rem;
  color: #666666;
  margin-bottom: 6px;
  font-style: italic;
}

.card.marked {
  border: 3px solid #4CAF50;
  background-color: #E8F5E9;
  position: relative;
}

.card.marked::after {
  content: "âœ”";
  font-size: 2rem;
  color: #4CAF50;
  position: absolute;
  top: 10px;
  right: 10px;
}
